.social-list__item {
  margin-top: 20px;
}

.social-list__fb {
  margin-left: -3px;
  margin-top: 3px;
}

.social-list__inst {
  margin-left: -6px;
  margin-top: 3px;
}

.social-list__vk:hover,
.social-list__inst:hover,
.social-list__fb:hover,
.social-list__vk:focus,
.social-list__inst:focus,
.social-list__fb:focus {
  fill: #68b738;
}

.social-list__vk:active,
.social-list__inst:active,
.social-list__fb:active {
  fill: #68b738;
  opacity: 0.3;
}

@media (min-width: $tablet-width) {
  .social-list__vk {
    width: 30px;
    height: 16px;
  }

  .social-list__inst {
    width: 23px;
    height: 23px;
    margin-left: -2px;
    margin-top: 0;
  }

  .social-list__fb {
    width: 14px;
    height: 28px;
    margin-top: -3px;
    margin-left: -3px;
  }

  .social-list__item:nth-child(2n) {
    margin-left: 28px;
    margin-right: 32px;
  }
}

@media (min-width: $desktop-width) {
  .social-list__item {
    margin-top: 30px;
  }
}
